@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&family=Roboto:wght@100;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.basicinfo {
  background-color: hsl(0, 0%, 100%, 70%);
  border-radius: 20px;
  text-align: center;
  height: 100%;
  margin: 10px;
  padding: 10px;

  font-family: "Open Sans Condensed", sans-serif;
  font-family: "Roboto", sans-serif;
  font-size: auto;
}
.aboutmee {
  background-color: hsl(0, 0%, 100%, 70%);
  border-radius: 20px;
  text-align: center;
  height: 100%;
  margin: 10px;
  padding: 10px;

  font-family: "Open Sans Condensed", sans-serif;
  font-family: "Roboto", sans-serif;
  font-size: auto;
}
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

#borderimg {
  border: 1px solid #555;
  border-radius: 150px;
  padding: 10px;
  background-image: linear-gradient(90deg, #4d0f00, #ffffff, #4d0f00, #ffffff);
}
.leftside {
  text-align: center;
}
.iconsize {
  font-size: 30px;
}
.link {
  color: black;
}

.aboutme {
  text-align: justify;
  text-justify: inter-word;
}

